import StepOne from './components/step-1.vue'
export default {
	data () {
		return {
			// 当前步骤
			currentStep: 0,
			certFront: [],
			certBack: []
		}
	},
	methods: {
		enter () {
			this.$JRequest('regi', {
				certType: this.certType,
				certNo: this.certNo,
				name: this.name,
				mobile: this.mobile
			}, data => {
				this.$JFn.showSuccess('登记信息成功')
			})
		}
	},
	components: { StepOne }
}
